<template>
  <div class="warpper">

    <div class="index">
      <div class="cont" :style="htmlSuccess?'':'height:60%;'">

        <!-- 中奖页面显示 -->
        <div v-if="htmlSuccess">
          <div class="desc-box">
            <div class="title">
              <img :src="img('success.png')" alt="">
            </div>
            <div class="desc">
              <img :src="img('success-desc.png')" alt="">
            </div>
          </div>
          <div class="product">
              <img :src="'http://wandafilm.hocodo.com/service/test/qrcode.php?text='+qrcode" alt="">
              <div class="text">{{qrcode}}</div>
          </div>
        </div>

        <!-- 未中奖页面显示 -->
        <div v-else>
          <div class="over">
            <img :src="img('over.png')" alt="">
          </div>
        </div>

        <div class="prize"></div>
      </div>
    </div>


  </div>
</template>

<script>
  export default {
    data(){
      return {
        qrcode:'124541234545',
        htmlSuccess:true
      }
    },
    methods:{
      img(e){
        return  require('../assets/images/'+ e)
      }
    }
 }
</script>

<style lang='scss' scoped>
  @import '../assets/style/global.scss';
  .warpper{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    @include bgCover("success-bg.png");
    .index{
      position: absolute;
      width: 100%;
      height: 100%;
      .cont{
        position: absolute;
        height: 83%;
        width: 100%;
        left: 0;
        top: 50%;
        transform: translateY(-44%);
        .desc-box{
          width: 5.09rem;
          margin: 0 auto;
          top: 0;
          img{
            width: 100%;
          }
        }
        .product{
          width: 2.68rem;
          height: 2.68rem;
          @include bgAuto("product.png");
          margin: 0.2rem auto .5rem auto;
          position: relative;
          img{
            width: 100%;
          }
          .text{
            font-size: 0.25rem;
            letter-spacing: 0.05rem;
            position: absolute;
            bottom: -.35rem;
            left: 50%;
            transform: translateX(-50%);
          }
        }
        .over{
          width: 5.02rem;
          margin: 0 auto;
          img{
            width: 100%;
          }
        }
        .prize{
          width: 6.25rem;
          height: 3.80rem;
          @include bgAuto("product.png");
          margin: 0 auto;
        }
      }

    }
    .mask{
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      background: rgba($color: #000000, $alpha: .8);
      z-index: 2;
      .share-allow{
        position: absolute;
        right: .75rem;
        top: .28rem;
        width: 2.55rem;
        height: 2.58rem;
        @include bgCover("arrow-top.png");
      }
    }
  }
</style>



