import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import result from '@/components/result'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/result',
      name: 'result',
      component: result
    }
  ]
})



