<template>
  <div id="app">
    <div class="logo">

    </div>
    <router-view/>
  </div>
</template>

<script>
import wx from 'weixin-js-sdk'

export default {
  name: 'App',
  data(){
    return {
      shareInfo:{
        appId: '',
        secret: '',
        url: window.location.href.split("#")[0]
      },
      shareData:{
        title:"欢迎关注我们广州轻工集团",
        desc:'广货2.0革新升级行动，更多惊喜等你来',
        imgUrl:'',
        link:'http://minisite.hocodo.com/apps/yinlifang'
      }
    }
  },
  created(){
    let that=this;
    let url="http://app.hocodo.com/webapps/weixinservice/weixinservice.php?callback=?";
    let sendData={"param": JSON.stringify(this.shareInfo)};
    this.$jsonp(url+'/company/Info', sendData ).then(data => {
  　　  // 返回数据 json， 返回的数据就是json格式
        wx.config({
            // debug: true,
            appId: data.appId,
            timestamp: data.timestamp,
            nonceStr: data.nonceStr,
            signature: data.signature,
            jsApiList: [
                // 所有要调用的 API 都要加到这个列表中
                'checkJsApi',
                'onMenuShareTimeline',
                'onMenuShareAppMessage',
                'onMenuShareQQ',
                'onMenuShareWeibo',
                'hideMenuItems',
                'showMenuItems',
                'hideAllNonBaseMenuItem',
                'showAllNonBaseMenuItem',
                'getNetworkType',
                'openLocation',
                'getLocation',
                'hideOptionMenu',
                'showOptionMenu',
                'closeWindow'
            ]
        });
        wx.ready(function () {
            // 在这里调用 API
            // 2. 分享接口
            // 2.1 监听“分享给朋友”，按钮点击、自定义分享内容及分享结果接口
            wx.hideAllNonBaseMenuItem();
            wx.onMenuShareAppMessage({
                title: that.shareData.title,
                desc: that.shareData.desc,
                link: that.shareData.link,
                imgUrl: that.shareData.imgUrl,
                trigger: function (res) {
                    //alert('用户点击发送给朋友');
                },
                success: function (res) {
                },
                cancel: function (res) {
                    //alert('已取消');
                },
                fail: function (res) {
                    //alert(JSON.stringify(res));
                }
            });

            // 2.2 监听“分享到朋友圈”按钮点击、自定义分享内容及分享结果接口

            wx.onMenuShareTimeline({
                title: that.shareData.title,
                link: that.shareData.link,
                imgUrl: that.shareData.imgUrl,
                trigger: function (res) {
                    // alert('用户点击分享到朋友圈');
                },
                success: function (res) {
                },
                cancel: function (res) {
                    // alert('已取消');
                },
                fail: function (res) {
                    // alert(JSON.stringify(res));
                }
            });
        }); //end of wx.ready
      }).catch(err => {
        console.log(err)
      })
  },
  methods:{

  },
  mounted(){


  }
}
</script>

<style>
/* #app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
} */
.logo{
  width: 2.42rem;
  height: 0.58rem;
  position: absolute;
  background: url(./assets/images/logo.png);
  background-size:100%;
  left: 0.5rem;
  top: 0.3rem;
}
</style>



