<template>
  <div class="warpper">

    <div class="index">
      <div class="cont">
        <div class="title"></div>
        <div class="product"></div>
        <div class="btn" @click="maskShow"></div>
      </div>
    </div>
    <div class="mask" v-show="maskShowState">
        <div class="share-allow">

        </div>
    </div>

  </div>
</template>

<script>
export default {
  data () {
    return {
      maskShowState:false
    }
  },
  methods:{
    maskShow(){
      this.maskShowState=true;
    }
  },
  mounted:()=>{
    console.log(2222);
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
  @import '../assets/style/global.scss';
  .warpper{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    @include bgCover("bg.png");
    .index{
      position: absolute;
      width: 100%;
      height: 100%;
      .cont{
        position: absolute;
        height: 70%;
        width: 100%;
        left: 0;
        top: 50%;
        transform: translateY(-50%);

      }
      .title{
        width: 5.24rem;
        height: 2.09rem;
        @include bgCover("title.png");
        top: 0rem;
        @include positionCenter("");
        position: absolute;
      }
      .product{
        position: absolute;
        width: 7.38rem;
        height: 4.53rem;
        top: 50%;
        @include bgAuto("product.png");
        @include positionCenter("center");
        transform: translate(-50%,-50%);
      }
      .btn{
        position: absolute;
        width: 3.76rem;
        height: 1.25rem;
        @include bgAuto("btn.png");
        bottom: 0rem;
        @include positionCenter("");
      }
    }
    .mask{
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      background: rgba($color: #000000, $alpha: .8);
      z-index: 2;
      .share-allow{
        position: absolute;
        right: .75rem;
        top: .28rem;
        width: 2.55rem;
        height: 2.58rem;
        @include bgCover("arrow-top.png");
      }
    }
  }
</style>



